<!-- 视频回放 -->
<template>
  <van-row>
    <!-- 顶部蓝色框 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col :span="20">
        <p class="dt-home-shouye">视频回放</p>
      </van-col>
    </van-row>
    <van-row>
      <van-form ref="form"
                v-model="form">
        <van-col span="16">
          <van-field v-model="form.DeviceName"
                     class="dt-input"
                     :left-icon="require('@/assets/img/搜索.png')"
                     placeholder="请输入设备名称" />
        </van-col>
        <van-col span="4">
          <van-button native-type="submit"
                      class="dt-button"
                      @click="getEquipmentList">搜索
          </van-button>
        </van-col>
        <van-col span="4">
          <div class="dt-saixuan">
            <img class="dt-saixuan-img"
                 @click="popupShow=true"
                 :src="require('@/assets/img/筛选.png')">
          </div>
        </van-col>
      </van-form>
    </van-row>
    <!-- 地图 -->
    <div>
      <videoPlayback :mapCenter="mapCenter"
                     :marketList="marketList">
      </videoPlayback>
    </div>
    <van-row>
      <van-row>
        <van-image :src="require('../../../assets/img/视频.png')"
                   width="159"
                   height="159"
                   class="dt-image">
        </van-image>
      </van-row>
      <van-row class="dt-font">未选择监控设备</van-row>
    </van-row>
    <!-- 弹出层 -->
    <van-popup v-model="popupShow"
               position="right"
               class="dt-popup">
      <van-row class="dt-pop-title-main">
        <span>筛选</span>
      </van-row>
      <van-row class="dt-pop-title">
        <span>选择区域</span>
      </van-row>
      <van-row>
        <van-field readonly
                   clickable
                   :value="areaName"
                   placeholder="请选择"
                   @click="showPicker = true" />
      </van-row>
      <van-row class="dt-pop-title-main">
        <span>筛选</span>
      </van-row>
      <van-row class="dt-pop-title">
        <span>选择时间</span>
      </van-row>
      <van-row gutter="20"
               align="bottom"
               class="dt-item-title">
        <van-col :span="12">
          <van-field readonly
                     clickable
                     :value="startTime | formatDate"
                     placeholder="开始时间"
                     @click="showPicker1 = true" />

        </van-col>
        <van-col :span="12">
          <van-field readonly
                     clickable
                     :value="endTime  | formatDate"
                     placeholder="结束时间"
                     @click="showPicker2 = true" />

        </van-col>
      </van-row>
      <van-row class="dt-botton-box">
        <div class="dt-bottons">
          <van-button round
                      class="dt-pop-button1"
                      type="info"
                      @click="filtrateCancel">取消</van-button>
          <van-button round
                      class="dt-pop-button2"
                      type="info"
                      @click="filtrateConfirm">确定</van-button>
        </div>
      </van-row>
    </van-popup>
    <van-popup v-model="showPicker"
               round
               position="bottom">
      <van-picker show-toolbar
                  :columns="areaList"
                  @cancel="showPicker = false"
                  @confirm="onConfirm" />
    </van-popup>
    <van-popup v-model="showPicker1"
               round
               position="bottom">
      <van-datetime-picker v-model="currentDate"
                           type="datetime"
                           title="选择开始时间"
                           :min-date="minDate"
                           :max-date="maxDate"
                           @cancel="showPicker1 = false"
                           @confirm="time1" />
    </van-popup>
    <van-popup v-model="showPicker2"
               round
               position="bottom">
      <van-datetime-picker v-model="currentDate"
                           type="datetime"
                           title="选择结束时间"
                           :min-date="minDate"
                           :max-date="maxDate"
                           @cancel="showPicker2 = false"
                           @confirm="time2" />
    </van-popup>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetRegionInfos, //获取区域列表
  GetEquipmentPagingList, // 视频回放
} from "@/api/mapApi";

import videoPlayback from "@/components/map/videoPlayback";
import { getLonLat } from "@/utils/map";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    videoPlayback
  },
  filters: {
    formatDate (date) {
      if (!date) {
        return '请选择日期'
      }
      let year = date.getFullYear()
      let month = date.getMonth() + 1
      let day = date.getDate()
      if (String(month).length < 2) {
        month = '0' + month
      }
      if (String(day).length < 2) {
        day = '0' + day
      }
      return String(year) + '-' + String(month) + '-' + String(day)
    },
  },
  data () {
    //这里存放数据
    return {
      form: {
        DeviceName: '',
        EquipmentType: "Monitor",
        RegionId: '',
        PageSize: 99999,
        PageIndex: 1
      },
      // 区域选择器
      // 区域选择器
      areaName: '',
      areaList: [],
      areasObj: {},
      showPicker: false,
      popupShow: false, // 弹出层是否显示
      //开始时间
      startTime: '',
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      showPicker1: false,
      //结束时间
      endTime: '',
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      showPicker2: false,
      //地图组件
      mapCenter: { lat: 32.33142324513, lng: 119.0877550222 },
      marketList: []
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getEquipmentList()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.getAreasList()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    //获取区域列表
    getAreasList () {
      GetRegionInfos({}).then(response => {
        let areas = response.Data
        for (let index in areas) {
          let area = areas[index]
          this.areaList.push(area.RegionName)
          this.areasObj[area.RegionName] = {
            RegionId: area.RegionId,
            Areas: area.Areas
          }
        }
      })

    },
    // 获取设备列表
    getEquipmentList () {
      console.log("设备列表请求参数", this.form);
      GetEquipmentPagingList(this.form).then(response => {
        console.log("设备列表返回!!!!", response)
        let PageContents = response.Data.PageContents
        PageContents = getLonLat(PageContents)
        this.marketList = []
        for (let index in PageContents) {
          let DeviceName = PageContents[index].DeviceName
          let Latitude = PageContents[index].Latitude
          let Longitude = PageContents[index].Longitude
          let info = {
            id: DeviceName,
            lat: Latitude,
            lng: Longitude
          }
          this.marketList.push(info)
        }
      })
    },

    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },
    // 选择区域确定事件
    onConfirm (value) {
      this.areaName = value;
      this.form.RegionId = this.areasObj[value].RegionId
      this.showPicker = false;
      console.log("选择的区域名称", this.areaName, this.areasObj[value]);
    },
    // 选择开始时间事件
    time1 (value) {
      this.startTime = value;
      this.showPicker1 = false;
      console.log("开始时间选择了", this.startTime)
    },
    // 选择结束时间事件
    time2 (value) {
      this.endTime = value;
      this.showPicker2 = false;
      console.log("结束时间选择了", this.endTime)
    },
    // 查看按钮事件
    query () {
      console.log("查看区域:", this.areaName, "名称:", this.form);
    },
    // 筛选确定按钮
    filtrateConfirm () {
      console.log("筛选了");
      this.areass = this.areassw
      this.popupShow = false
      this.getEquipmentList()
    },
    // 筛选取消按钮
    filtrateCancel () {
      this.popupShow = false
      this.areaName = ''
      this.form.RegionId = ''
    },
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
