<!-- 视频回放地图 -->
<template>
  <div class="dt-main">
    <div id='equMon'></div>

    <div class="dt-checkBox">
      <van-checkbox v-model="checked1"
                    @click="check1"
                    icon-size="15px">摄像头</van-checkbox>

    </div>
  </div>
</template>
<script>

var map
var cameraMarker
let infowindow
export default {
  props: ['mapCenter', 'marketList', 'areaName', 'equipmentName'],
  data () {
    //这里存放数据
    return {
      checked1: true,

      childMapCenter: {},
      markList: []
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    mapCenter (newValue, oldValue) {
      this.childMapCenter = newValue
      this.$forceUpdate()
    },
    marketList (newValue, oldValue) {
      this.markList = newValue
      this.set()
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.childMapCenter = this.mapCenter
    this.markList = this.marketList
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.markList = this.marketList
    this.initMap()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前

  updated () {
    this.setMapCenter()
  },
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    initMap () {
      var center = new TMap.LatLng(this.childMapCenter.lat, this.childMapCenter.lng)//定义地图中心点坐标

      map = new TMap.Map('equMon', {//定义map变量，调用 TMap.Map() 构造函数创建地图
        center: center,//设置地图中心点坐标
        zoom: 15.5,   //设置地图缩放级别
        viewMode: '2D'
      });
      // 定义摄像头点标记
      cameraMarker = new TMap.MultiMarker({
        id: '摄像头',
        map: map,
        //样式定义
        styles: {
          //创建一个styleId为"myStyle"的样式（styles的子属性名即为styleId）
          "myStyle": new TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/摄像头图标.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 17, y: 17 }
          })
        },
        //点标记数据数组
        geometries: this.getGeometries()
      })


      //监听回调函数（非匿名函数）
      var clickHandler = function (evt) {
        if (infowindow) {
          //设置infoWindow
          infowindow.open(); //打开信息窗
          infowindow.setPosition(evt.geometry.position);//设置信息窗位置
          infowindow.setContent(evt.geometry.position.toString());//设置信息窗内容
          infowindow.close();//初始关闭信息窗关闭
        }
        var infoWindowLocation = evt.geometry.position;//创建一个坐标
        //创建InfoWindow实例，并进行初始化
        infowindow = new TMap.InfoWindow({
          map: map,
          content: evt.geometry.properties.title, //信息窗口内容
          position: infoWindowLocation,//显示信息窗口的坐标
          offset: { x: 0, y: -17 } //设置信息窗相对position偏移像素，为了使其显示在Marker的上方
        });

      }

      //摄像头 
      cameraMarker.on("click", clickHandler)

    },
    //
    setMapCenter () {
      var center = new TMap.LatLng(this.childMapCenter.lat, this.childMapCenter.lng);
      map.setCenter(center)
    },

    getGeometries () {
      let list = []
      for (let index in this.markList) {
        let market = this.markList[index]
        list.push({
          "id": market.id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyle',  //指定样式id
          "position": new TMap.LatLng(market.lat, market.lng),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.id
          }
        })
      }
      return list
    },
    // 地图按钮
    check1 () {
      if (this.checked1) {
        cameraMarker.add(this.getGeometries())

      } else {
        cameraMarker.setGeometries([])
        if (infowindow) {
          infowindow.close();//初始关闭信息窗关闭
        }
      }
    },
    set () {
      cameraMarker.setGeometries([])
      cameraMarker.add(this.getGeometries())
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../styles/dimens";
.dt-main {
  position: relative;
  width: 100%;
  height: px2rem(339);
  #equMon {
    width: 100%;
    height: px2rem(339);
  }
  .dt-checkBox {
    display: inline-block;
    position: absolute;
    z-index: 1000;
    right: 0;
    bottom: 0;
    .van-checkbox {
      float: right;
      font-size: px2rem(15);
      margin-right: px2rem(5);
      margin-top: px2rem(5);
      z-index: 1000;
    }
  }
}
</style>